import * as React from "react";
import { NavLink } from "react-router-dom";

import "./styles/navigation.css";

/**
 * @prop {boolean} [show]   .
 */
interface IProps {
    show?: boolean;
}

/**
 *   .
 */
export class Navigation extends React.Component<IProps, {}> {
    render() {
        const {show} = this.props;

        if (!show) {
            return <div></div>;
        }

        return (
            <nav className="nav">
                <ul className="navigation">
                    <li className="navigation_link">
                        <NavLink className="link" activeClassName="link__active" to="/portfolio/glavnaya" title="">
                            
                        </NavLink>
                    </li>
                    <li className="navigation_link">
                        <NavLink className="link" activeClassName="link__active" to="/price" title="">
                            
                        </NavLink>
                    </li>
                    <li className="navigation_link">
                        <NavLink className="link" activeClassName="link__active" to="/blog" title="">
                            
                        </NavLink>
                    </li>
                    <li className="navigation_link">
                        <NavLink className="link" activeClassName="link__active" to="/contacts" title="">
                            
                        </NavLink>
                    </li>
                </ul>
            </nav>
        );
    }
}